.qualification {
  padding: 8rem 22.4rem;
}

/*Timeline*/
.timeline {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3.2rem;
}
.timeline .timeline-item {
  position: relative;
  padding-left: 4.8rem;
  border-left: 0.1rem solid var(--color-grey-5);
}
.timeline .timeline-item .tl-icon {
  position: absolute;
  left: -2.7rem;
  top: 0;
  background-color: var(--color-secondary);
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.timeline .timeline-item .tl-icon i {
  font-size: 2.08rem;
}
.timeline .timeline-item .tl-duration {
  padding: 0.32rem 0.96rem;
  background-color: var(--color-grey-5);
  border-radius: 15px;
  display: inline-block;
  font-size: 1.28rem;
  text-transform: uppercase;
  font-weight: 500;
}
.timeline .timeline-item h5 {
  padding: 1.6rem 0;
  font-size: 2.08rem;
  font-weight: 600;
  color: var(--color-grey0);
}
.timeline .timeline-item h5 span {
  color: var(--color-grey-2);
  font-weight: 500;
  font-size: 1.92rem;
}
.timeline .timeline-item p {
  color: var(--color-grey-3);
  font-size: 1.6rem;
}

.right-about .btn-con {
  position: relative;
  top: 1.6rem;
  left: 28.8rem;
}
.qualification .abt-text{
position: relative;
bottom: -3.5rem;
left: 3.5rem;  
}
@media screen and (max-width: 480px){
  .qualification .abt-text {
    position: relative;
    width: 45rem;
    bottom: 1rem;
    left: 1rem;
  }
  .qualification {
    position: relative;
    right: 5rem;
    top: -20rem;
  }

}

 