.Contact {
  padding: 11.2rem;
  overflow: hidden;
  bottom: 5rem;

}
.contact-container {
  position: relative;
  left: 16rem;
  width: 60%;
  display: grid;
  grid-template-columns: 40% 58%;
  gap: 12%;
}

/*========================================= left ====================*/

.contact-container .left-contact {
  display: flex;
  flex-direction: column;
  gap: 1.92rem;
}
.contact-container .left-contact .social-link {
  background-color: var(--color-grey-5);
  color: var(--color-white);
  padding: 1.92rem;
  border-radius: 1.92rem;
  text-align: center;
  border: .1rem solid transparent;
  transition: var(--trans1);
}
.contact-container .left-contact .social-link a {
  text-decoration: none;
  margin: 1.12rem;
  display: inline-block;
  font-size: 1.6rem;
  color: var(--gray);
}
.contact-container .left-contact .social-link:hover {
  background: transparent;
  border-color: var(--trans1);
}
.contact-container .left-contact .social-link .social-icon {
  font-size: 2.4rem;
  margin-bottom: 2.4rem;
}
h4{
  font-size: 1.6rem;
}
h5{
  font-size: 1.3rem;
}

/*yaha tak hua hai*/

/*========================================= right ====================*/

.contact-container .right-contact {
  display: flex;
  justify-content: center;
  position: relative;
  flex: 1;
}
form {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  align-items: center;
  position: relative;
  top: 0.8rem;
}
form span {
  padding-top: 4.8rem;
  color: var(--color-white);
}

.right-contact .user {
  width: 48rem;
  height: 4.8rem;
  padding: 1.6rem;
  outline: none;
  border: 0.2rem solid var(--color-grey-5);
  border-radius: 0.8rem;
  font-size: 1.6rem;
  background: transparent;
  color: var(--color-white);
  resize: none;
}

textarea {
  height: 12.8rem !important;
}
@media screen and (max-width: 480px){
.Contact{
  position: relative;
  bottom:20rem;
  right: 4rem;
}
 
  .contact-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    top:10rem;
    
  }
  form span{
    position: relative;
    bottom:2rem;
    font-size: 1.6rem;
  }
  #cont{
    top:6rem;
    left:8rem;
  }
 
 

}

 