* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}

:root {
  --color-secondary: #fa20d2;
  --color-white: #ffffff;
  --color-black: #000;
  --color-grey0: #f8f8f8;
  --color-grey-1: #dbe1e8;
  --color-grey-2: #b2becd;
  --color-grey-3: #6c7983;
  --color-grey-4: #454e56;
  --color-grey-5: #3f0033;
  --color-grey-6: #12181b;
  --lightblue: #40ffff;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --trans: linear-gradient(90deg, #fa20d2 26.71%, #80106b 99.36%);
  --trans1: #fa20d2;
  --trans2: linear-gradient(180deg, #fa20d2 26.71%, #80106b 99.36%);
}
section {
  margin-top: 11.2rem;
  height: 100vh;
}

:root {
  background-image: url("C:/Users/tanwe/OneDrive/Desktop/html to react/my-portfolio/src/img/pattern1.png");
