.footer {
  display: flex;
  align-items: center;
  margin: -1.6rem 0 -4.8rem;
  position: relative;
  overflow: hidden;
}
.f-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 4.8rem;
  gap: 6.4rem;
  color: white;
  font-size: 1.6rem;
}

.f-content .lower {
  position: relative;
  top: 8rem;
  color: var(--color-grey-5);
  font-size: 1.6rem;
}
.f-icon {
  position: relative;
  left: 62.4rem;
  bottom: 7.25rem;
  display: flex;
  gap: 1.6rem;
}
.f-icon a {
  font-size: 3.2rem;
  color: var(--color-white);
}
.f-icon a:hover {
  transform: scale(1.5);
}
@media screen and (max-width: 480px){
  .footer {
    
    margin-top: 10rem;
    
  }
  .f-content {
    top:1rem;
    font-size: 1rem;
    
    }
  .f-icon {
    position: relative;
    bottom: 1rem;
    left: 23rem;
    
  }
}

