.container {
  padding: 13rem 22.4rem 1.6rem;
}

/*Skills*/
.skills-container {
  position: relative;
  padding-left: 10rem;
  bottom: 8rem;
}
.skills-container .right-skills {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3.2rem;
}
.skills-container .right-skills .skills-item {
  border: 1px solid var(--color-grey-5);
  background-color: var(--blueCard);
  width: 12.8rem;
  height: 14.4rem;
  border-radius: 100%;
  margin-top: 3.2rem;
  margin-bottom: 8rem;
}
#js {
  transform: scale(35%);
  right: 4rem;
}
#bootstrap {
  transform: scale(26%);
  right: 12.16rem;
}
.skills-container .right-skills .skills-item:hover {
  cursor: default;
  transform: translateY(-0.5rem);
  border: .2rem solid var(--color-secondary);
  box-shadow: 0.1rem 0.4rem 1.5rem rgba(0, 0, 0, 0.32);
  background-color: rgba(255, 253, 253, 0.32);
}

.skills-container .right-skills .skills-item .large-text img {
  position: relative;

  right: 8.5rem;
  bottom: 8rem;
  transform: scale(0.4);
}

.skills-container .right-skills .skills-item .abt-text {
  padding: 6.4rem;
  display: flex;
  flex-direction: column;
  
}

.skills-container .right-skills .skills-item .abt-text .small-text{
  padding-left: 4.8rem;
  position: relative;
  right: 8rem;
  bottom: 22.4rem;
  font-size: 1.92rem;
  color: var(--color-grey-1);
  text-align: center;

}



.skills-container .right-skills .skills-item .abt-text .small-text span {
  font-size: 1.5rem;
  color: var(--gray);
}
.skills-container .right-skills .skills-item .abt-text .small-text::before {
  content: "";
  position: absolute;
  left: 3.2rem;
  bottom: 1.8rem;
  width: 10.4rem;
  height: .2rem;
  background-color: var(--color-grey-5);
}
.main-title {
  position: relative;
  right: 3rem;
  bottom: 11.5rem;
  font-size: 6.4rem;
  color: #ddd;
  text-align: center;
  font-weight: 700;
}
.main-title .temp{
  font-size: 1.6rem;
  color: var(--gray);
  
}


.skills-container .right-skills .btn-con {
  position: relative;
  top: 1rem;
  left: 30.4rem;
}
@media screen and (max-width: 480px){
  .container {
    display: flex;
    flex-direction: column;
    gap: 6.4rem;
    position: relative;
    top: -15rem;
    left: -25rem;
    
  }
  #sk-title {
  width: 48rem;
  position: relative;
  right: 10rem;;
    
  }
  .skills-container .right-skills .btn-con {
    width: 16rem;
    position: relative;
    left: 18rem;
    top: 10rem;
  }
 .skills-container .right-skills .skills-item .large-text img {
    position: relative;
  
    right: 14.5rem;
     bottom: 14rem;
    transform: scale(0.3);
  }
  #js {
    transform: scale(25%);
    right:8.4rem;
  }
  #bootstrap {
    transform: scale(20%);
    right: 20rem;
  }
  .skills-container .right-skills .skills-item .abt-text .small-text {
  
    position: relative;
    right: 8rem;
    bottom: 32rem;
    
  }
  .skills-container .right-skills .skills-item{
    position: relative;
    left:3rem;
  }
}
@media screen and (max-width: 1024px){
 
  .container {
    display: flex;
    flex-direction: column;
    gap: 6.4rem;
    position: relative;
    bottom: -10rem;
    right: 5rem;
    margin-top: 40rem;
    
  }
  .title {
  width: 48rem;
  position: relative;
  left: 25rem;;
    
  }
  .skills-container .right-skills .btn-con {
    width: 16rem;
    position: relative;
    right: 10rem;
    top: 10rem;
  }
 .skills-container .right-skills .skills-item .large-text img {
    position: relative;
  
    right: 14.5rem;
     bottom: 14rem;
    transform: scale(0.3);
  }
  #js {
    transform: scale(25%);
    right:8.4rem;
  }
  #bootstrap {
    transform: scale(20%);
    right: 20rem;
  }
  .skills-container .right-skills .skills-item .abt-text .small-text {
  
    position: relative;
    right: 8rem;
    bottom: 32rem;
    
  }
  .skills-container .right-skills .skills-item{
    position: relative;
    left:3rem;
  }
}

