.navigation {
  left: 92%;
  top: 20rem;

  width: 7rem;
  height: 40rem;

  background: var(--trans);
  display: block;
  align-items: center;
  border-radius: 1rem;
  position: fixed;
}
.navigation ul {
  display: table-row;
  width: 35rem;
}
.navigation ul li {
  position: relative;
  list-style: none;
  width: 7rem;
  height: 7rem;
  z-index: 1;
}
.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  text-align: center;
  font-weight: 500;
}
.navigation ul li a .icon {
  position: relative;
  display: block;
  line-height: 12rem;
  font-size: 2.4rem;
  text-align: center;
  transition: 0.5s;
  color: white;
}
.navigation ul li.active a .icon {
  transform: translateX(-5.5rem);
}
.navigation ul li a .text {
  position: absolute;
  color: white;
  font-weight: 400;
  font-size: 1.2rem;
  letter-spacing: 0.08rem;
  transition: 0.5s;
  opacity: 0;
  transform: translateX(2rem);
}
.navigation ul li.active a .text {
  opacity: 1;
  transform: translateX(.4rem);
}
.indicator {
  position: absolute;
  justify-content: right;
  top: 6%;
  right: 77%;
  width: 7rem;
  height: 7rem;
  background: var(--trans1);
  border-radius: 50%;
  transition: 0.5s;
  z-index: -1;
}
.indicator::before {
  content: "";
  position: absolute;
  width: 3rem;
  height: 3rem;
  top: -3rem;
  left: 2.4rem;
  background: transparent;
  border-radius: 50%;
  box-shadow: 1.5rem 1.8rem #fa20d2;
}
.indicator::after {
  content: "";
  position: absolute;
  width: 3rem;
  height: 3rem;
  bottom: -3rem;
  left: 2.4rem;
  background: transparent;
  border-radius: 50%;
  box-shadow: 1.6rem -1.6rem #fa20d2;
}
.navigation ul li:nth-child(1).active ~ .indicator {
  transform: translateY(calc(7rem * 0));
}
.navigation ul li:nth-child(2).active ~ .indicator {
  transform: translateY(calc(7rem * 1));
}
.navigation ul li:nth-child(3).active ~ .indicator {
  transform: translateY(calc(7rem * 2));
}
.navigation ul li:nth-child(4).active ~ .indicator {
  transform: translateY(calc(7rem * 3));
}
.navigation ul li:nth-child(5).active ~ .indicator {
  transform: translateY(calc(7rem * 4));
}
  

@media screen and (max-width: 480px){
  .navigation{
    display:none;
  }
}
@media screen and (max-width: 1024px){
  .navigation{
    display: none;
    
  }
  
}

