.header-content {
  font-family: "Poppins", sans-serif;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
  font-size: 1.76rem;
}
/*header-content*/

.header-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-bottom: 8rem;
  padding-left: 3.2rem;
}
.header-content .left-header {
  display: flex;
  align-items: center;
  position: relative;
}

.header-content .right-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-right: 19.2rem;
}
.header-content .right-header .name {
  font-size: 4.8rem;
}
.header-content .right-header .name span {
  color: var(--trans1);
}
.header-content .right-header p {
  margin: 2.4rem 0rem;
  line-height:3.2rem;
}

.btn-con {
  position: relative;
  top: 10rem;
  left: 6.5rem;
}
.main-btn {
  background: linear-gradient(180deg, #fa20d2 26.71%, #80106b 99.36%);
  box-shadow: 0px 1.8rem 1.1rem 0.1rem rgba(196, 0, 174, 0.42);
  border-radius: 3.4rem;
  border: none;
  color: white;
  font-size: 1.6rem;
  padding: 1.1rem 1.8rem 1.1rem 1.8rem;
}
.main-btn .btn-text {
  padding: 0 3.2rem;
}
.btn-con a {
  text-decoration: none;
}

.main-btn::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  transform: tanslateX(100%);
  transition: all 0.4s ease-out;
  z-index: -1;
}
.main-btn:hover {
  transition: all 0.4s ease-out;
  background: white;
  cursor: pointer;
  border: .1rem solid var(--trans1);
  color: var(--trans1);
}

.header-content .left-header .image > :nth-child(2) {
  height: 100%;
  width: 95%;
  position: absolute;
  z-index: -2;
  top: -3.5rem;
  left: -3.5rem;
}
.header-content .left-header .image > :nth-child(3) {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 7rem;
  left: .5rem;
}

.header-content .left-header .image > :nth-child(1) {
  position: relative;
  height: 80%;
  width: 80%;
  top: -1.3rem;
  left: 5rem;
}
.header-content .left-header {
  margin-top: 8rem;
}
.header-content .right-header {
  margin-top: 10rem;
}

.i-icon {
  position: absolute;
  left: 8.8rem;
  margin-top: 72rem;
  transform: scale(0.47);
  display: flex;
  gap: 3.2rem;
}
.i-icon > *:hover {
  cursor: pointer;
  transform: scale(1.2);
}
@media screen and (max-width: 480px){
  html{
    font-size: 45%;
  }
  .header-content {
    display: flex;
    flex-direction: column;
    gap: 25rem;
    
  }
  .Floatingdiv {
    position: relative;
    top: -55rem;
    right: 9rem;
    transform: scale(0.9);
  }
  .Floatingdiv > :nth-child(2) {
    
    position: absolute;
    margin-left: 7rem;

  }
  .Floatingdiv > :nth-child(3) {
    
    position: absolute;
    margin-left: 6rem;

  }
  .Floatingdiv > :nth-child(5) {
    
    position: absolute;
    margin-left: -4rem;
    transform: scale(0.14);

  }
  .i-icon{
    position: absolute;
    right: 25rem;
  }
  .header-content .right-header {
    position: relative;
    left: 10rem;
  }
 


}
@media screen and (max-width: 1024px) {
  .Floatingdiv {
    position: relative;
    bottom: 60rem;
    right: 9rem;
    transform: scale(0.9);
  }
  .Floatingdiv > :nth-child(1) {
    
    position: absolute;
    margin-left: 7rem;

  }
  .Floatingdiv > :nth-child(2) {
    
    position: absolute;
    margin-left: 7rem;

  }
  .Floatingdiv > :nth-child(3) {
    
    position: absolute;
    margin-left: 6rem;

  }
  .Floatingdiv > :nth-child(5) {
    
    position: absolute;
    margin-left: -4rem;
    transform: scale(0.14);

  } 
  .Floatingdiv > :nth-child(6) {
    
    position: absolute;
    margin-left: -2rem;
    

  } 
  .i-icon{
    position: absolute;
    right: 25rem;
  } 
}



